import React, { PureComponent } from 'react'
import {
	Table,
	Divider,
	Modal,
	Input,
  Image,
  Card,
  Button
} from 'antd'
import { dispatch } from '../../utils/fetch'
import CreateApplets from './addApplets'

const servers = 'http://ci.gokuaidian.com:4100/services/applets'

const { TextArea } = Input

const STYLES = {
	FORM: {
		display: 'flex',
		justifyContent: 'center',
  	alignItems: 'center',
		marginBottom: '10px',
		LABEL: {
			width: '50px'
		},
		INPUT: {
			width: '50%'
		}
	}
}

export default class Index extends PureComponent {

	constructor(props) {
		super(props)
		this.state = {
			visible: false,
      showQrcode: false,
      addVisible: false,
			loading: false,
			data: [],
			record: {},
			version: null,
			desc: null,
			robot: 1,
      qrcode: '',
      pagePath: '',
      applets: {},
			columns: [
				{
					title: '名称',
					dataIndex: 'applets_name',
					key: 'applets_name'
				},
				{
					title: '版本',
					dataIndex: 'version',
					key: 'version',
				},
        {
					title: '描述',
					dataIndex: 'descss',
					key: 'descss',
				},
				{
					title: '操作',
					key: 'action',
					render: (record) => {
						return (
							<div>
								<span
                  style={{color: '#1890ff', cursor: 'pointer'}}
                  onClick={() => { this.handleShowModalPreview(record) }}>预览 </span>
								<Divider  type="vertical" />
								<span style={{color: '#1890ff', cursor: 'pointer'}} onClick={() => { this.handleShowModal(record) }}>上传 </span>
							</div>
						)
					},
				},
			]
		}
	}

	componentDidMount () {
		this.setState({
			loading: true
		}, () => {
			this.getAppletsList()
		})
	}

	getAppletsList () {
		dispatch(`${servers}/getAppletsList`, {}).then(res => {
			this.setState({
				data: res && res.result,
				loading: false
			})
		})
	}

	uploadApplets (id) {
    this.setState({
      loading: true
    })
		dispatch(`${servers}/uploadApplets`, {
			appid: id,
			version: this.state.version,
			descss: this.state.descss,
      robot: this.state.robot,
      key: this.state.record.key
		}).then(res => {
      if (res.code === '200') {
        this.getAppletsList()
      }
      this.setState({
        version: null,
        descss: null
      })
		})
	}

	previewApplets (id) {
		dispatch(`${servers}/previewApplets`, {
			appid: id,
			desc: '这是一段描述',
      pagePath: this.state.pagePath,
		}).then(res => {
      this.setState({
        qrcode: res.data.qrcode
      })
		})
	}

	handleShowModal (record) {
    let version = record.version.split('.')[record.version.split('.').length - 1]
    let newVersion = record.version.split('.')
    newVersion[newVersion.length - 1] = version * 1 + 1
		this.setState({
			visible: true,
			record: record,
      version: newVersion.join('.'),
      descss: record.descss
		})
	}

  handleShowModalPreview (record) {
		this.setState({
			showQrcode: true,
      record: record,
			pagePath: record.path
		})
	}

	handleOk () {
		this.setState({
			visible: false,
		}, () => {
			this.uploadApplets(this.state.record.appid)
		})
	}

	handleCancel () {
		this.setState({
			visible: false
		})
	}

  handleOk1 () {
		this.setState({
			showQrcode: false,
		}, () => {
      this.previewApplets(this.state.record.appid)
    })
	}

	handleCancel1 () {
		this.setState({
			showQrcode: false
		})
	}

	handleChangeVersion (e) {
		this.setState({
			version: e.target.value
		})
	}

	handleChangeDesc (e) {
		this.setState({
			descss: e.target.value
		})
	}

	handleChangeRobot (e) {
		this.setState({
			robot: e.target.value
		})
	}

  handleInsetApplets () {
    this.setState({
      addVisible: true
    })
  }

  handleSubmit () {
    this.setState({
      addVisible: false
    })
  }

  renderModal () {
		const { visible } = this.state
		return (
			<Modal
				visible={visible}
				onOk={this.handleOk.bind(this)}
				onCancel={this.handleCancel.bind(this)}
			>
				<div
					style={STYLES.FORM}>
						<div style={STYLES.FORM.LABEL}
					>
						版本号: 
					</div>
					<Input
						style={STYLES.FORM.INPUT}
						placeholder="请输入版本号"
						value={this.state.version}
						onChange={this.handleChangeVersion.bind(this)}
					/>
				</div>
				<div
					style={STYLES.FORM}>
					<div
						style={STYLES.FORM.LABEL}
					>
						描述: 
					</div>
					<TextArea
						onChange={this.handleChangeDesc.bind(this)}
						value={this.state.descss}
						style={STYLES.FORM.INPUT} />
				</div>
				<div
					style={STYLES.FORM}>
					<div
						style={STYLES.FORM.LABEL}
					>
						机器人: 
					</div>
					<Input
						style={STYLES.FORM.INPUT}
						placeholder="请输入数字"
						value={this.state.robot}
						onChange={this.handleChangeRobot.bind(this)}
					/>
				</div>
			</Modal>
		)
	}

  renderModalQrCode () {
		const { showQrcode } = this.state
		return (
			<Modal
				visible={showQrcode}
				onOk={this.handleOk1.bind(this)}
				onCancel={this.handleCancel1.bind(this)}
			>
        <div style={{textAlign: 'center'}}>
          <div
            style={STYLES.FORM}>
              <div style={STYLES.FORM.LABEL}
            >
              版本号: 
            </div>
            <Input
              style={STYLES.FORM.INPUT}
              placeholder="请输入版本号"
              value={this.state.version}
              onChange={this.handleChangeVersion.bind(this)}
            />
          </div>
          {/* <Image
            width={200}
            src={qr}
            preview={{
              src: qr,
            }}
          /> */}
        </div>
			</Modal>
		)
	}

	render () {
		const { columns, data, loading, qrcode, addVisible } = this.state
		return (
			<div>
        <Card>
          <div ><Button type="primary" onClick={this.handleInsetApplets.bind(this)}>添加</Button></div>
          <div style={{paddingTop: 10 }}>
            <Table
              columns={columns}
              dataSource={data || []}
              loading={loading}
            />
          </div>
        </Card>
				{this.renderModal()}
        {this.renderModalQrCode()}
        <CreateApplets
          visible={addVisible}
          onOk={this.handleSubmit.bind(this)}
        />
        <Image
          width={200}
          src={qrcode}
          preview={{
            src: qrcode,
          }}
        />
			</div>
		)
	}
}
